<template>
  <div class="tyh-input" :class="prohibitClass">
    <Tyh-icon
      v-if="showIcon"
      class="tyh-input-icon__showIcon"
      :class="[iconHeightClass]"
      :icon="showIcon"
      size="12"
      color="rgb(199, 199, 199)"
    />
    <input
      class="tyh-input_inp"
      :class="[sizeClass, clearClass, prohibitClass, paddingLeftClass]"
      :type="inpType"
      :value="value"
      :placeholder="innerText"
      :maxlength="max"
      :disabled="prohibit"
      :autofocus="autofocus"
      :name="name"
      @input="handleInput"
    />
    <Tyh-icon
      v-if="clear"
      class="tyh-input-icon__clear"
      :class="[iconHeightClass]"
      size="12"
      color="rgb(199, 199, 199)"
      icon="tyh-ui-close-03"
      @click="clearInputText"
    />
  </div>
</template>

<script>
import TyhIcon from '../../icon/src/icon'
export default {
  name: 'TyhInput',
  components: {
    TyhIcon
  },
  props: {
    //  v-model  value 
    value: [String, Number],
    // 
    innerText: String,
    // 
    inpType: {
      type: String,
      default: 'text',
      validator: function (value) {
        // 
        return ['text', 'password'].indexOf(value) !== -1
      }
    },
    // 
    size: {
      type: String,
      default: 'medium'
    },
    // 
    max: String,
    // 
    clear: {
      type: Boolean,
      default: false
    },
    // 
    showIcon: String,
    // 
    prohibit: {
      type: Boolean,
      default: false
    },
    // 
    autofocus: {
      type: Boolean,
      default: false
    },
    //  name 
    name: String
  },
  computed: {
    //  class
    sizeClass () {
      return this.size
        ? `tyh-input_inp--${this.size}`
        : 'tyh-input_inp--medium'
    },
    // 
    clearClass () {
      return this.clear ? 'tyh-input-clear-padding' : ''
    },
    // 
    iconHeightClass () {
      return `tyh-input-icon-${this.size}-height`
    },
    // 
    prohibitClass () {
      return this.prohibit ? 'tyh-button-prohibit' : ''
    },
    // 
    paddingLeftClass () {
      return this.showIcon ? 'tyh-input__padding-left' : ''
    }
  },
  methods: {
    //  input   
    handleInput (evt) {
      //  input  input  emit 
      this.$emit('input', evt.target.value)
    },
    // 
    clearInputText () {
      this.$emit('input', '')
    }
  }
}
</script>

<style src="../style/index.css" scoped></style>
